.cuadro-container{
    display: -webkit-flex;
    display: flex;
    
}
.forma-circulo{
    border-radius:50%;
    overflow: hidden;
    width: 40%;
    height:0;
    padding-bottom: 40%;
    margin:  0 auto;
    border: 10px solid rgb(43, 42, 42);
    
}
.cuadro{
    width: 100%;
    height: 0;
    position: relative;
    padding-bottom: 55%;
    
  
}

.opacity{
    opacity: 0.5;
}
.amarillo{
    background: yellow;
    border-left: 5px solid rgb(43, 42, 42);
    border-bottom: 5px solid rgb(43, 42, 42);
}
.rojo{
    background: red;
    border-right: 5px solid rgb(43, 42, 42);
    border-top: 5px solid rgb(43, 42, 42);
}
.azul{
    background: blue; 
    border-right: 5px solid rgb(43, 42, 42);
    border-bottom: 5px solid rgb(43, 42, 42);
}

.verde{
    background:greenyellow;
    border-left: 5px solid rgb(43, 42, 42);
    border-top: 5px solid rgb(43, 42, 42);
}
.verde:hover{
    opacity: 0.7;
}
.azul:hover{
    opacity: 0.7;
}
.amarillo:hover{
    opacity: 0.7;
}
.rojo:hover{
    opacity: 0.7;
}

@media screen and (max-width:800px) {
  .forma-circulo{
    margin-top: 20vh;
  } 
  .score{
      font-size: 25px;
  }
}
.container-setup{
    margin-top:30vh ; 

    
}
.titulo-setup{
    color:black;
    font-size: 30px;
    text-align: center;
}

.subtitulo-setup{
    color:black;
    font-size: 20px;
    text-align: center;
}


.boton-setup{
    margin: 0 auto!important; 
    display: block!important;
    
}
